.notes__main-content {
    display: flex;
    flex-direction: column;
    height: 90vh;
}


.notes__appbar {
    align-items: center;
    background-color: $primary;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
}


.notes__content {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
}


.notes__title-input, .notes__textarea {
    border: none;

    &:focus{
        outline: none;
    }
}

.notes__title-input {
    color: $dark-grey;
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 10px;
}

.notes__textarea {
    border: none;
    color: $dark-grey;
    font-size: 20px;
    height: 150px;
    resize: none;
}

.notes__image img {
    box-shadow: 5px 5px $dark-grey;
    height: 180px;
    margin-top: 50px;
    transition: all 0.5s linear;
}

.notes__image img:hover {
    transform: scale(1.03);
}


@media only screen and (max-width: 600px) {
    .notes__textarea {
        height: 250px;
    }
}